<template>
    <div id="text_group">
        <div id="input_group">
            <input 
                :type="type"
                :value="value"
                :placeholder="placeholder"
                @input="$emit('input',$event.target.value)"
            >
            <button
                v-if="btnTitle" 
                :disabled="disabled"
                @click="$emit('btnClick')"
            >{{btnTitle}}</button>
        </div>
        <div v-if="error">{{error}}</div>
    </div>
</template>
<script>
export default {
    name:"inputGroup",
    props:{
        type:{type:String,default:"text"},
        value:String,
        placeholder:String,
        btnTitle:String,
        disabled:Boolean,
        error:String
    }
}
</script>
<style scoped>
    #input_group{
        width: 250px;
        border: 1px solid skyblue;
        margin: 20px;
    }
    #input_group input{
        width: 150px;
        height: 30px;
        border: 0;
        outline: none;
        
    }
    #input_group button{
        border: 0;
        outline: none;
        background: #fff;
    }

</style>