<template>
    <div id="app">
        <img src="../assets/logo.jpg" alt="">
        <InputGroup
            type="number"
            placeholder="手机号"
            v-model="phone"
            :btnTitle="btnTitle"
            :disabled="disabled"
            :error="errors.phone"
            @btnClick="getVerufycode"
        ></InputGroup>
        <InputGroup
            type="number"
            v-model="verifyCode"
            :disabled="disabled"
        ></InputGroup>
        <button class="admin" :disabled="isClick" @click="handleLogin">登录</button>
    </div>
    
</template>
<script>
import InputGroup from "../components/inputGroup"
export default {
    name:"login",
    data(){
        return{
            phone:"",
            verifyCode:"",
            btnTitle:"获取验证码",
            disabled:false,
            errors:{}
        }
    },
    components:{
        InputGroup
    },
    methods:{
        //手机号合法性
        validatePhoen(){
            console.log("......")
            if(!this.phone){
                //手机号为空
                this.errors={
                    phone:"手机号不能为空"
                }
                return false
            }else if(!/^1[345678]\d{9}$/.test(this.phone)){
                this.errors = {
                    phone:"手机号不合法"
                }
                return false
            }else{
                this.errors={}
                return true
            }
        },
        //倒计时60秒
        validateBtn(){
            let time = 60;
            let timer = setInterval(()=>{ 
                if(time == 0){
                    clearInterval(timer)
                    this.btnTitle='获取验证码'
                    this.disabled = false
                }else{
                    this.btnTitle=time+'秒后重试'
                    this.disabled=true
                    time--
                }
            },1000)
        },
        getVerufycode(){
            if(this.validatePhoen()){
                this.validateBtn()
                console.log("...")
                this.$axios.post('/api/posts/sms_send',{
                    tpl_id:"213063",
                    key:"f29c1f363af00744e8232303eda2644e",
                    phone:this.phone
                }).then(res=>{
                    console.log(res)
                })
            }
            
        },
        //实现登录逻辑
        handleLogin(){
            this.errors={};
            this.$axios.post('/api/posts/sms_back',{
                phone:this.phone,
                code:this.verifyCode
            }).then(res=>{
                localStorage.setItem('ele_login',true)//如果OK，储存一个登录成功标致
                this.$router.push('/')     
            }).catch(err=>{
                this.errors={code:err.resphonse.data.msg};
                console.log(err)
            })
        }
    },
    computed:{
        isClick(){
            if(!this.phone || !this.verifyCode){
                return true
            }else{
                return false
            }
        }
    }
}
</script>
<style scoped>
    #app InputGroup{
        display: block;
        margin-top: 10px;
    }
    .admin{
        width: 60px;
        background-color: skyblue;
        margin-left: 110px;
        border: 0;
    }
</style>